<template>
  <div class="bOrders">
    <div class="bOrders__container container pt-4">
      <div class="bOrders__header row">
        <div class="bOrders__header__h2 h2 col">訂單管理</div>
      </div>
      <div class="bOrders__body pt-3 d-flex flex-column align-items-center">
        <table class="table">
          <thead>
            <tr>
              <th scope="col" style="vertical-align:middle;"
              class="text-center">
                <button class="btn btn-sm btn-primary"
                @click.prevent="send_orders">
                  出貨
                </button>
              </th>
              <th scope="col" style="vertical-align:middle;">希望收件日期</th>
              <th scope="col" style="vertical-align:middle;">訂貨人</th>
              <th scope="col" style="vertical-align:middle;">收貨人</th>
              <th scope="col" style="vertical-align:middle;">總金額</th>
              <th scope="col" style="vertical-align:middle;">已付款</th>
              <th scope="col" style="vertical-align:middle;">功能</th>
            </tr>
          </thead>
          <tbody v-for="(item, index) in orders"
          :key="index">
            <tr
            v-b-toggle="`collapse${index}`"
            :class="[{'table-info': item.payment.ifPaid},
                    {'table-warning': !item.payment.ifPaid}]">
              <td style="vertical-align:middle;" class="text-center">
                <span v-if="!item.sending.ifSent">
                  <span v-if="item.payment.ifPaid">
                    <input type="checkbox"
                    @change='push_sendingList($event.target.checked, item.id)'>
                  </span>
                </span>
                <span class="d-flex flex-column" v-if="item.sending.ifSent">
                  <span>已出貨</span>
                  <span>{{convert_time(item.sending.info.sentTime)}}</span>
                </span>
              </td>
              <td style="vertical-align:middle;">{{item.recipient.esdDate}}</td>
              <td style="vertical-align:middle;">{{item.buyer.name}}</td>
              <td style="vertical-align:middle;">
                <span v-if="item.recipient.ifGetFromStore">
                  門市自取
                </span>
                <span v-if="!item.recipient.ifGetFromStore">
                  {{item.recipient.info.name}}
                </span>
              </td>
              <td style="vertical-align:middle;">{{item.sum}}</td>
              <td style="vertical-align:middle;">{{convert_text(item.payment.ifPaid)}}</td>
              <td style="vertical-align:middle;">
                <div v-if="!item.sending.ifSent">
                  <button class="btn btn-sm btn-danger m-1"
                  v-if="item.payment.ifPaid">
                    取消並退款
                  </button>
                </div>
                <button class="btn btn-sm btn-danger m-1"
                v-if="!item.payment.ifPaid"
                @click.prevent='confirm_delete_admin(item.id)'>
                  取消訂單
                </button>
              </td>
            </tr>
            <tr>
              <td colspan="100%" class="py-0 border-0">
                <b-collapse :id='`collapse${index}`'>
                  <b-tabs class="py-3" content-class="py-3">
                    <b-tab title="訂單資料">
                      <p class="mb-1">
                        訂單建立日期: {{convert_time(item.createTime)}}
                      </p>
                      <p class="mb-1">
                        訂購人: {{item.buyer.name}}
                      </p>
                      <p class="mb-1">
                        訂購人電話: {{item.buyer.tel}}
                      </p>
                      <p>
                        訂購人Email: {{item.buyer.email}}
                      </p>
                    </b-tab>
                    <b-tab title="購買產品" active>
                      <table class="table">
                        <thead>
                          <tr>
                            <th scope="col">產品ID</th>
                            <th scope="col">產品名稱</th>
                            <th scope="col">產品數量</th>
                            <th scope="col">產品單價</th>
                          </tr>
                        </thead>
                        <tbody>
                          <tr v-for="(cItem, cIndex) in item.cart.products"
                          :key="cIndex">
                            <td style="vertical-align:middle;">
                              {{cItem.id}}
                            </td>
                            <td style="vertical-align:middle;">
                              <div class="d-flex align-items-center">
                                <div class="boxSize-100-100 mr-3">
                                  <img :src="cItem.img" alt="" class="w-100 h-100">
                                </div>
                                <p>{{cItem.title}}</p>
                              </div>
                            </td>
                            <td style="vertical-align:middle;">
                              {{cItem.quantity}}
                            </td>
                            <td style="vertical-align:middle;">
                              NT$ {{cItem.price}}
                            </td>
                          </tr>
                        </tbody>
                      </table>
                    </b-tab>
                    <b-tab title="優惠券">
                        <p v-if="item.coupon.id === ''">
                          無
                        </p>
                      <table class="table" v-if="item.coupon.id !== ''">
                        <thead>
                          <tr>
                            <th scope="col">優惠券ID</th>
                            <th scope="col">優惠券描述</th>
                            <th scope="col">折價</th>
                          </tr>
                        </thead>
                        <tbody>
                          <tr>
                            <td>{{item.coupon.id}}</td>
                            <td>{{item.coupon.description}}</td>
                            <td>{{item.coupon.discount}}</td>
                          </tr>
                        </tbody>
                      </table>
                    </b-tab>
                    <b-tab title="寄送資訊">
                      <div v-if="item.recipient.ifGetFromStore">
                        <ul>
                          <li>到門市自取</li>
                          <li>預定取貨日期: {{item.recipient.esdDate}}</li>
                          <li>預定取貨時間: {{convert_text(item.recipient.esdTime)}}</li>
                        </ul>
                      </div>
                      <div v-if="!item.recipient.ifGetFromStore">
                        <p>收件人資訊</p>
                        <ul>
                          <li>收件人姓名: {{item.recipient.info.name}}</li>
                          <li>收件人電話: {{item.recipient.info.tel}}</li>
                          <li>收件人地址: {{item.recipient.info.address}}</li>
                          <li>
                            是否有公司 / 大樓代收: {{convert_text(item.recipient.info.ifRepresentative)}}
                          </li>
                          <li v-if="item.recipient.info.ifRepresentative"
                          >收花人公司 / 大樓名稱: {{item.recipient.info.representativeTitle}}</li>
                          <li>匿名配送: {{convert_text(item.recipient.info.ifAnonymous)}}</li>
                        </ul>
                      </div>
                    </b-tab>
                    <b-tab title="付款資訊">
                      <ul>
                        <li>
                          已付款: {{convert_text(item.payment.ifPaid)}}
                        </li>
                        <li v-if="item.payment.ifPaid">
                          付款時間: {{item.payment.info.time}}
                        </li>
                        <li v-if="item.payment.ifPaid">
                          付款方式: {{convert_text(item.payment.info.type)}}
                        </li>
                        <li v-if="item.payment.ifPaid">
                          信用卡號碼: {{item.payment.info.creditNumber}}
                        </li>
                      </ul>
                    </b-tab>
                    <b-tab title="發票資訊">
                      <ul>
                        <li>發票類型: {{convert_text(item.invoice.type)}}</li>
                        <li v-if="item.invoice.type === 'person'">
                          發票載具: {{convert_text(item.invoice.personCarrier)}}</li>
                        <li v-if="item.invoice.type === 'person'">
                          <span v-if="item.invoice.personCarrier === 'no'">
                            中獎發票寄送地址: {{item.invoice.personCarrierAddress}}
                          </span>
                        </li>
                        <li v-if="item.invoice.type === 'person'">
                          <span v-if="item.invoice.personCarrier === 'mobile'">
                            手機條碼載具: {{item.invoice.personCarrierMobile}}
                          </span>
                        </li>
                        <li v-if="item.invoice.type === 'person'">
                          <span v-if="item.invoice.personCarrier === 'nature'">
                            自然人憑證載具: {{item.invoice.personCarrierNature}}
                          </span>
                        </li>
                        <li v-if="item.invoice.type === 'donation'"
                        >捐贈單位: {{convert_text(item.invoice.donationTarget)}}</li>
                        <li  v-if="item.invoice.type === 'company'">
                          公司抬頭: {{item.invoice.companyTitle}}</li>
                        <li v-if="item.invoice.type === 'company'">
                          公司統一編號: {{item.invoice.companyId}}</li>
                      </ul>
                    </b-tab>
                  </b-tabs>
                </b-collapse>
              </td>
            </tr>
          </tbody>
        </table>
      </div>
    </div>
    <b-modal id='confirm_delete_order'
    title="確認"
    ok-title='是'
    cancel-title='否'
    @ok='delete_order(deletingOrderId)'>
      <p class="my-3">確定要刪除嗎?</p>
    </b-modal>
  </div>
</template>

<script>
export default {
  data() {
    return {
      orders: [],
      sendingList: [],
      deletingOrderId: '',
    };
  },
  methods: {
    initialize_orders() {
      let orderArr;
      this.$bus.$on('son_get_data', (data) => {
        orderArr = JSON.parse(JSON.stringify(data.orders));
      });
      this.$bus.$emit('son_call_data');
      const convertEsdDate = (esdDate) => {
        const newDate = new Date(esdDate);
        const newDateNum = newDate.getTime();
        return newDateNum;
      };
      orderArr.sort((a, b) => {
        const numA = convertEsdDate(a.recipient.esdDate);
        const numB = convertEsdDate(b.recipient.esdDate);
        return numB - numA;
      });
      this.orders = orderArr;
    },
    convert_time(timeNum) {
      const theDate = new Date(timeNum);
      const year = theDate.getFullYear();
      const month = theDate.getMonth() + 1;
      const day = theDate.getDate();
      const hour = theDate.getHours();
      const minute = theDate.getMinutes();
      const second = theDate.getSeconds();
      const twoDigits = (num) => (num > 9 ? '' : '0') + num;
      const timeStr = `${year}-${twoDigits(month)}-${twoDigits(day)} ${twoDigits(hour)}:${twoDigits(minute)}:${twoDigits(second)}`;
      return timeStr;
    },
    convert_text(value) {
      let text;
      switch (value) {
        case true:
          text = '是';
          break;
        case false:
          text = '否';
          break;
        case '':
          text = '不指定';
          break;
        case 'morning':
          text = '上午( 08:00 ~ 13:00 )';
          break;
        case 'afternoon':
          text = '下午( 14:00 ~ 18:00 )';
          break;
        case 'person':
          text = '個人電子發票';
          break;
        case 'donation':
          text = '捐贈電子發票';
          break;
        case 'company':
          text = '公司報帳電子發票';
          break;
        case 'no':
          text = '不使用載具';
          break;
        case 'mobile':
          text = '手機條碼載具';
          break;
        case 'nature':
          text = '自然人憑證載具';
          break;
        case 'donationCompany1':
          text = '捐贈單位1';
          break;
        case 'donationCompany2':
          text = '捐贈單位2';
          break;
        case 'donationCompany3':
          text = '捐贈單位3';
          break;
        case 'creditCard':
          text = '信用卡';
          break;
        default:
          break;
      }
      return text;
    },
    push_sendingList(checkboxBoolean, orderId) {
      if (checkboxBoolean) {
        // 預備出貨
        this.sendingList.push(orderId);
      } else {
        // 取消預備出貨
        const sendingListIndex = this.sendingList.findIndex((item) => item === orderId);
        this.sendingList.splice(sendingListIndex, 1);
      }
    },
    send_orders() {
      this.$bus.$emit('send_order', this.sendingList);
      this.initialize_orders();
    },
    confirm_delete_admin(orderIndex) {
      this.deletingOrderId = orderIndex;
      this.$bvModal.show('confirm_delete_order');
    },
    delete_order(orderId) {
      this.$bus.$emit('delete_order', orderId);
      this.initialize_orders();
    },
  },
  created() {
    this.initialize_orders();
  },
};
</script>
